.dashboard-body {
  color: #2b323a;
  text-decoration: none;

  background-color: #ffffff;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  -webkit-clip-path: inset(100%);

          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.main-site-navigation {
  display: flex;

  margin-bottom: 30px;

  font-family:  Arial, sans-serif;
  font-size: 25px;

  border-bottom: 1px solid #2b323a;
}

.main-site-navigation-name {
  align-self: center;
  padding-right: 20px;

  color: inherit;
  text-decoration: none;
}

.main-site-navigation-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 200px;
  margin: 0;
  margin-left: auto;
  padding: 0;

  list-style: none;
}

.main-site-navigation-list a {
  position: relative;
  display: block;
  padding: 30px 0;

  vertical-align: middle;
  color: #2b323a;

  text-decoration: none;
}

.main-site-navigation-list a:hover::after,
.main-site-navigation-list a:focus::after {
  content: "";

  position: absolute;
  left: 0;
  top: 99%;
  width: 100%;
  height: 2px;

  background-color: #ff0000;
}

.main-site-navigation-logo:focus,
.main-site-navigation-list a:focus {
  outline: 1px solid #ffffff;
}

.mentor-select {
  margin-bottom: 30px;
}

.find-mentor-button:hover {
  outline: 2px solid #87cefa;
}

.dashboard-table {
  margin: 0 auto;
  margin-bottom: 30px;
  font-size: 20px;

  border-collapse: collapse;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.dashboard-table caption {
  margin-bottom: 10px;

  font-size: 25px;
  font-weight: bold;
}

.dashboard-table td,
.dashboard-table th {
  padding: 5px 10px;
  border: 1px solid #2b323a;
}

.dashboard-table a {
  font: inherit;
  color: inherit;
  text-decoration: none;
}

.dashboard-table a:hover,
.dashboard-table a:focus{
  text-decoration: underline;
}

.task-status-in-progress {
  background-color: #faebd7;
}

.task-status-checking {
  background-color: #ff69b4;
}

.task-status-checked {
  background-color: #90ee90;
}

.task-status-todo {
  background-color: #add8e6;
}


.task-status-checking-time-gone {
  background-color: #ff0000;
}


